<template>
    <v-flex>
        <template v-if="windowWidth > 960">
            <br>
            <br>
            <H2> Diplomes obtenus : </H2>
            <br>
            <p v-for="(diplome, index) in diplomes"
               :key="`diplome-${index}`"
            > {{ diplome }} </p>
        </template>

        <template v-if="windowWidth < 960">
            <v-expansion-panels flat>
                <v-expansion-panel>
                    <v-expansion-panel-header>Diplomes obtenus </v-expansion-panel-header>
                    <v-expansion-panel-content>
                        <v-list disabled three-line>
                            <v-list-item-group color="primary">
                                <v-list-item
                                        v-for="(diplome, index) in diplomes"
                                        :key="`diplome-${index}`"
                                        inactive
                                >
                                    <v-list-item-content>
                                        <v-list-item-title v-html="diplome"></v-list-item-title>
                                    </v-list-item-content>
                                </v-list-item>
                            </v-list-item-group>
                        </v-list>
                    </v-expansion-panel-content>
                </v-expansion-panel>
            </v-expansion-panels>
        </template>
    </v-flex>
</template>

<script>
    import axios from "axios";

    export default {
        name: "diplome",

        data: function () {
            return {
                details_file: "files/details.json",
                diplomes: null,
                windowWidth: window.innerWidth,
            }
        },
        methods: {
            getDip: function () {
                const self = this;
                axios.get(this.details_file).then(function (result) {
                    self.diplomes = result.data.diplomes;
                });
            }
        },
        mounted() {
            this.getDip();
            window.onresize = () => {
                this.windowWidth = window.innerWidth
            }
        }
    }
</script>

